@keyframes blink {
  from, to {
    border-color: #0000;
  }

  50% {
    border-color: #05ff00;
  }
}

@keyframes main_background {
  0% {
    -webkit-backdrop-filter: hue-rotate();
    backdrop-filter: hue-rotate();
    background-position: 0 75%;
  }

  50% {
    background-position: 75% 150%;
  }

  100% {
    -webkit-backdrop-filter: hue-rotate(360deg);
    backdrop-filter: hue-rotate(360deg);
    background-position: 0 75%;
  }
}

@keyframes main_ani_title {
  0% {
    text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 16px #fff;
  }

  100% {
    text-shadow: 0 0 #fff, 0 0 #fff, 0 0 #fff;
  }
}

@keyframes minecraft-shake {
  0%, 100% {
    transform: translateX(0)perspective(1000px)rotateX(15deg);
  }

  10%, 30%, 50%, 70% {
    transform: translateX(-10px)perspective(1000px)rotateX(15deg);
  }

  20%, 40%, 60% {
    transform: translateX(10px)perspective(1000px)rotateX(15deg);
  }

  80% {
    transform: translateX(8px)perspective(1000px)rotateX(15deg);
  }

  90% {
    transform: translateX(-8px)perspective(1000px)rotateX(15deg);
  }
}

@keyframes shake-vertical {
  0%, 100% {
    transform: translateY(0);
  }

  10%, 30%, 50%, 70% {
    transform: translateY(-8px);
  }

  20%, 40%, 60% {
    transform: translateY(8px);
  }

  80% {
    transform: translateY(6.4px);
  }

  90% {
    transform: translateY(-6.4px);
  }
}

@keyframes heartbeat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(.91);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(.98);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(.87);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

@keyframes stars {
  from {
    transform: translateX(-1000px)translateY(0)scale(1.75);
  }

  to {
    transform: translateX(-1000px)translateY(-2000px)scale(1.75);
  }
}

@keyframes coin {
  from {
    opacity: 0;
    transform: translateY(0);
  }

  to {
    opacity: 1;
    transform: translateY(-180px);
  }
}

@keyframes note {
  0% {
    opacity: 0;
    transform: translateY(0);
  }

  0% {
    opacity: 0;
    transform: translateY(-120px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 1;
    transform: translateY(-220px);
  }
}

@keyframes laser {
  0% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(0);
  }

  12.5% {
    fill-opacity: 1;
    opacity: 1;
    transform: translateY(-40px);
  }

  87.5% {
    fill-opacity: 1;
    opacity: 1;
  }

  100% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(-1100px);
  }
}

@keyframes laser-short {
  0% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(0);
  }

  12.5% {
    fill-opacity: 1;
    opacity: 1;
    transform: translateY(-40px);
  }

  87.5% {
    fill-opacity: 1;
    opacity: 1;
  }

  100% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(-1100px);
  }
}

@keyframes laser-long {
  0% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(0);
  }

  12.5% {
    fill-opacity: 1;
    opacity: 1;
  }

  87.5% {
    fill-opacity: 1;
    opacity: 1;
    transform: translateY(-10000px);
  }

  100% {
    fill-opacity: 0;
    opacity: 0;
    transform: translateY(-1000000px);
  }
}

html {
  scroll-behavior: smooth;
}

.scroll-content {
  width: 100%;
}

.scrollbar-track {
  z-index: 9999 !important;
  background: none !important;
}

::-webkit-scrollbar {
  display: none;
}

#body {
  width: 100%;
  max-height: 100vh;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  overflow-x: hidden;
}

#body > .scroll-content {
  width: 100%;
  max-height: 100vh;
  z-index: 4000;
}

#body:has(#app > #start), #body:has(#app > #credits) {
  color: #05ff00;
  font-family: Inconsolata;
}

#nav {
  width: 100%;
  flex-direction: row;
  display: flex;
  position: absolute;
  top: 0;
}

#nav:has( + #app > #start), #nav:has( + #app > #credits) {
  display: none;
}

#app > #main {
  background: linear-gradient(270deg, #9f0000, #009f00, #00009f) 0 0 / 600% 600%;
  animation: 30s infinite main_background;
}

#app > #exit {
  height: 100vh;
  background-size: 600% 600%;
}

#app, #start, #credits, #decades {
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

#start, #credits {
  height: 100vh;
  background-color: #000;
  overflow: hidden;
}

.navbar {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem 1.75rem;
  display: flex;
}

.navbar > .navbar-menu {
  flex-direction: row;
  justify-content: space-evenly;
  gap: 1.75rem;
  display: flex;
}

.console {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 4rem;
  display: flex;
}

.console > .console-header, .console > .console-terminal {
  padding: 1.75rem;
}

.console > .console-header {
  flex-direction: column;
  gap: 1.75rem;
  font-size: 2.125rem;
  display: flex;
}

.console > .console-header .indent:nth-child(n+2) {
  margin-left: 1rem;
}

.console > .console-header > .console-title {
  flex-direction: column;
  display: flex;
}

.console > .console-header > .console-instructions {
  flex-direction: column;
  gap: 1rem;
  margin-left: 1.5rem;
  display: flex;
}

.console > .console-header > .console-credits {
  flex-direction: column;
  gap: 1.25rem;
  margin-left: 1.5rem;
  display: flex;
}

.console > .console-header > .console-credits > .credits-category {
  flex-direction: column;
  gap: 1rem;
  margin-left: 1.5rem;
  font-size: 1.625rem;
  display: flex;
}

.console > .console-header > .console-credits > .credits-category > .category {
  flex-direction: column;
  display: flex;
}

.console > .console-header > .console-credits > .credits-category > .credits {
  flex-direction: column;
  gap: .5rem;
  margin-left: 1.5rem;
  display: flex;
}

.console > .console-header > .console-credits > .credits-category > .credits > .credit {
  flex-direction: row;
  gap: 1rem;
  text-decoration: none;
  display: flex;
}

.console > .console-header > .console-credits > .credits-category > .credits > .credit:before {
  content: "- ";
}

.console > .console-terminal {
  flex-direction: column;
  justify-content: flex-end;
  font-size: 2rem;
  display: flex;
}

.console > .console-terminal > .terminal-popup {
  max-width: 90ch;
  flex-direction: row;
  justify-content: flex-start;
  display: flex;
}

.console > .console-terminal > .terminal {
  line-break: anywhere;
  max-width: 90ch;
  flex-flow: wrap;
  justify-content: flex-start;
  gap: .5rem;
  display: flex;
}

.console > .console-terminal > .terminal:not(:last-child) > .terminal-container > .caret {
  display: none;
}

.console > .console-terminal > .terminal > .terminal-container {
  max-width: 100%;
  flex-direction: row;
  justify-content: flex-start;
  display: flex;
}

.console > .console-terminal > .terminal > .terminal-container > .terminal-cli {
  color: #05ff00;
  caret-color: #0000;
  width: 0;
  max-width: 90ch;
  background-color: #000;
  border: none;
  flex-wrap: wrap;
  font-family: Inconsolata;
  display: flex;
  overflow: auto;
}

.console > .console-terminal > .terminal > .terminal-container > .terminal-cli:focus-visible {
  outline-width: 0;
}

.console > .console-terminal > .terminal > .terminal-container > .caret {
  border-bottom: .5rem solid #05ff00;
  padding: 1rem;
  animation: 1s step-end infinite blink;
}

#main {
  color: #fff;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Odibee Sans;
  display: flex;
}

#main > .main-screen {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14rem;
  display: flex;
}

#main > .main-screen .title {
  -webkit-user-select: none;
  user-select: none;
}

#main > .main-screen .title .ani-title {
  animation: 7.5s cubic-bezier(.645, .045, .355, 1) infinite alternate main_ani_title;
}

#decades .decade {
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

#decades .decade > .title-screen, #decades .decade > .title-screen > .header, #decades .decade > .game-screen, #decades .decade > .game-screen > .game-grid {
  width: 100%;
  height: 110vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#decades .decade > .title-screen > .header {
  font-size: 16rem;
}

#decades .decade > .title-screen > .header > .svg-chars {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: absolute;
}

#decades .decade > .title-screen > .header > .svg-chars > .svg-char {
  position: relative;
}

#decades .decade > .title-screen > .header .svg-char {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: absolute;
}

#decades .decade > .title-screen > .header .svg-char:has( > .galaxian) {
  transform: translateY(450px);
}

#decades .decade > .title-screen > .header .svg-char > .galaxian {
  width: 10vmin;
  overflow: visible;
}

#decades .decade > .title-screen > .header .svg-char > .galaxian .left-laser, #decades .decade > .title-screen > .header .svg-char > .galaxian .right-laser {
  opacity: 0;
  fill-opacity: 0;
  position: absolute;
}

#decades .decade > .title-screen > .header .svg-char:has( > .among-us) {
  transform: translateY(0);
}

#decades .decade > .title-screen > .header .svg-char > .among-us {
  width: 20vmin;
  transform: scale(2.5)translateX(-480px);
}

#decades .decade > .title-screen > .header .svg-char:has( > .pacman) {
  transform: translateY(0);
}

#decades .decade > .title-screen > .header .svg-char > .pacman {
  z-index: 5555;
  width: 20vmin;
  transform: scale(2.5)translateX(-480px);
}

#decades .decade > .title-screen > .header .svg-char:has( > .mario-coin) {
  opacity: 0;
  gap: .75rem;
  padding: .75rem;
}

#decades .decade > .title-screen > .header .svg-char > .mario-coin {
  width: 13.75vmin;
}

#decades .decade > .title-screen > .header .svg-char:has( > .ocarina-note) {
  opacity: 0;
  gap: .75rem;
  padding: .75rem;
}

#decades .decade > .title-screen > .header .svg-char > .ocarina-note {
  width: 12.75vmin;
  transform: scale(.75);
}

#decades .decade > .title-screen > .header > .title {
  -webkit-user-select: none;
  user-select: none;
  z-index: 7777;
}

